export default "/* ****************************** NAVIGATION BAR ****************************** */\r\n/* Both top and bottom parts of navbar */\r\n.navigation {position:fixed;width:100%;z-index:99;border-bottom:1px solid lightgrey}\r\n.navigation a {color:#727272; padding:12px 15px;font-family:Helvetica,sans-serif;font-size:12pt}\r\n.navigation a:hover {transition:0.7s ease;color:#272727;color:#272727;text-decoration:none}\r\n/* The dark thick top-border */\r\n#header {overflow:hidden;background-color:#333;opacity:1;border-bottom:1px solid black; top:0;margin-top:-12px}\r\n/* The actual navigation with links */\r\n#navbar {overflow:hidden;opacity:1;}\r\n.navbar-toggle {background-color: #272727;}\r\n/* #navbarCollapse {text-align:center;padding-left:280px;} */\r\n/* Github, linkedIn and contact pages */\r\n.social-links {margin-top:25px}\r\n.social-links a {padding:25px;}\r\n/* Screen sizes - from biggest to smallest */\r\n/* Bigger screens */\r\n@media screen and (min-width:767px) {\r\n  .hide-links {display:block}\r\n  .blog-link {display:none}\r\n}\r\n/* Only medium devices, eg ipads but not desktops #1 */\r\n/* @media screen and (min-width:1101px) and (max-width:1400px) {\r\n  .separator-vertical {display:block}\r\n  .hide-links {display:none}\r\n  .blog-link {display:block}\r\n}  */\r\n/* Only medium devices, eg ipads but not desktops #2 */\r\n/* @media screen and (min-width:901px) and (max-width:1100px) {\r\n  .separator-vertical {display:none}\r\n  .hide-links {display:none}\r\n  .blog-link {display:block}\r\n  #navbarCollapse {text-align:center;padding-left:150px;}\r\n}  */\r\n/* Only medium devices, eg ipads but not desktops #3 */\r\n/* @media screen and (min-width:767px) and (max-width:900px) {\r\n  .separator-vertical {display:none}\r\n  .hide-links {display:none}\r\n  .blog-link {display:block}\r\n  #navbarCollapse {text-align:center;padding-left:20px;}\r\n}  */\r\n/* Everything after smaller screen, eg ipad, desktop */\r\n@media screen and (min-width:76